
import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import { Provider } from 'react-redux';
import { createHashHistory } from 'history';
import { ConnectedRouter } from 'connected-react-router';
import { createRoot } from 'react-dom/client';
import 'react-app-polyfill/ie11';
import 'react-app-polyfill/stable';
import registerServiceWorker from './registerServiceWorker';
import configureStore from './store/configureStore';

const base = document.querySelector('base');
const baseUrl = base && base.href || '';
const history = createHashHistory({basename: baseUrl});
const initialState = window.initialReduxState;
const store = configureStore(history, initialState);
const rootElement = document.getElementById('root');

const configuration = {
  applicationName: 'CofC - 1.0.0',
  applicationAcronym:'CFC',
  folder:''
};
const root = createRoot(<Provider store={store}>
   <ConnectedRouter history={history}>
    <App />
  </ConnectedRouter>
  </Provider>,
  rootElement); // createRoot(container!) if you use TypeScript
root.render();
registerServiceWorker();
